import http from '@/utils/myhttp.js'
// 
export function apiGetChannel () {
  return http({
    url: '/user/channels'
  })
}
// 
export function apiGetAllChannel () {
  return http({
    url: '/channels'
  })
}
// 
export function apiRedactChannel (arr) {
  return http({
    method: 'put',
    url: '/user/channels',
    data: {
      channels: arr
    }
  })
}
