<template>
  <div class="more">
    <van-popup v-model="show" :style="{ width: '90%' }">
      <van-cell-group class="listicon" v-if="isInform">
        <van-cell @click="dislikes">
          <template #title>
            <van-icon name="failure" class="ii" />
          </template>
        </van-cell>
        <van-cell is-link @click.stop="isInform=false">
          <template #title>
            <van-icon name="warning-o" class="ii" />
          </template>
        </van-cell>
        <van-cell @click="blacklists">
          <template #title>
            <van-icon name="delete" class="ii" />
          </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group v-else>
        <van-cell>
          <template #title>
            <van-icon name="arrow-left" @click.stop="isInform=true" />
          </template>
        </van-cell>
        <van-cell
          v-for="(item, index) in inform"
          :key="index"
          :title="item.type"
          @click.stop="reports(item.id)"
        ></van-cell>
      </van-cell-group>
    </van-popup>
  </div>
</template>

<script>
import { apiDislikes, apireports, apiBlacklists } from '@/api/articles.js'
export default {
  data () {
    return {
      show: false,
      // 
      inform: [
        { id: 1, type: '' },
        { id: 2, type: '' },
        { id: 3, type: '' },
        { id: 4, type: '' },
        { id: 5, type: '' },
        { id: 6, type: '' },
        { id: 7, type: '' },
        { id: 8, type: '' },
        { id: 0, type: '' }
      ],
      isInform: true
    }
  },
  props: ['val', 'num'],
  methods: {
    // 
    dislikes () {
      try {
        apiDislikes(this.val.art_id.toString()).then(res => {
          if (res.data.message === 'OK') {
            this.$emit('del')
            this.show = false
          }
        })
      } catch {
        this.$toast.fail('')
      }
    },
    // 
    reports (id) {
      try {
        apireports({
          target: this.val.art_id.toString(),
          type: id,
          remark: ''
        }).then(res => {
          console.log(res)
          if (res.data.message === 'OK') {
            this.show = false
            this.isInform = true
            this.$toast.success('')
          }
        })
      } catch {
        this.$toast.fail('')
      }
    },
    // 
    blacklists () {
      try {
        apiBlacklists(this.val.aut_id).then(res => {
          if (res.data.message === 'OK') {
            this.$toast.success('')
            this.show = false
          }
        })
      } catch {
        this.$toast.fail('')
      }
    }
  }
}
</script>

<style lang="less">
.more {
  .listicon {
    .ii {
      margin-right: 10px;
      transform: translateY(2px);
    }
  }
}
</style>
