import http from '@/utils/myhttp.js'
// 
export function apiGetChannel () {
  return http({
    url: '/user/channels'
  })
}

// 
export function apiRedactChannel (arr) {
  return http({
    method: 'put',
    url: '/user/channels',
    data: {
      channels: arr
    }
  })
}
// 
export function apifollowings (id) {
  return http({
    method: 'post',
    url: '/user/followings',
    data: {
      target: id
    }
  })
}
// 
export function apicallOff (id) {
  return http({
    method: 'DELETE',
    url: '/user/followings/' + id
  })
}
// 
export function apiUser () {
  return http({
    url: '/user'
  })
}
// 
export function apiUserProfile () {
  return http({
    url: '/user/profile'
  })
}
// 
export function apiUserCompile (obj) {
  return http({
    method: 'PATCH',
    url: '/user/profile',
    data: obj
  })
}
// 
export function apiUserPhoto (obj) {
  var fd = new FormData()
  fd.append('photo', obj)
  return http({
    method: 'PATCH',
    url: '/user/photo',
    data: fd
  })
}
